body, header, a, img, div, nav, ul, li {
  margin: 0;
}

:root {
  --black: #000;
  --blue: #00acdc;
}

body {
  text-transform: uppercase;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  padding: 0 50px;
  display: flex;
}

.logo {
  align-self: center;
  padding: 10px 0;
  display: flex;
}

.nav {
  justify-content: flex-end;
  display: flex;
}

.nav__list {
  justify-content: flex-end;
  padding-left: 0;
  list-style-type: none;
  display: flex;
}

.nav__item__link {
  color: var(--black);
  align-self: center;
  padding: 22.5px 0;
  text-decoration: none;
  display: flex;
}

.nav__item {
  text-transform: uppercase;
  margin-right: 20px;
}

.nav__item:last-child {
  margin: 0;
}

.nav__item__link:hover {
  color: var(--blue);
}

.nav__item:last-of-type .nav__item__link {
  margin-right: 0;
}

.is-active {
  color: var(--blue);
  position: relative;
}

.nav__item .is-active {
  color: var(--blue);
}

.is-active:after {
  content: "";
  background: var(--blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: -1px;
}
